import './App.scss';

import { HashRouter, Route } from 'react-router-dom';

import Home from './routes/Home'
import Privacy from './routes/Privacy'

function App() {
  return (
    <HashRouter basename='/'>
      <Route exact path="/" component={Home} />
      <Route path="/privacy-policy" component={Privacy} />
    </HashRouter>
  );
}

export default App;
